@use "global" as *;

.card{
  display: block;
  max-width: rem(350);
  width: 100%;
  margin: auto;
  transition: ease .3s;
  @include mq(md) {
    margin: initial;
  }
}

.card:last-of-type {
  margin-right: initial;
}

.card__img{
  padding-top: 66.5671642%;
  position: relative;
  @include mq(md) {
    padding-top: 66.5714286%;
  }
}
.card__img img{
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.card__body{
  background: $white;
  padding: rem(2) rem(21) rem(16);
  @include mq(md) {
    padding: rem(4) rem(23) rem(16);
  }
}
.card:hover .card__body{
background: $text-color;
transition: ease .3s;
}
.card__title{
  font-size: rem(18);
  line-height: 1.944;
  letter-spacing: 1px;
}
.card:hover .card__title{
  color: $white;
}
.card__text{
  font-size: rem(14);
  line-height: 1.857;
  letter-spacing: 1px;
  margin-top: rem(4);
  @include mq(md) {
    margin-top: rem(6);
  }
}
.card:hover .card__text{
  color: $white;
}
.card__info{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: rem(6);
  @include mq(md) {
    margin-top: rem(9);
  }
}
.card__category{
  background: #FFEA2E;
  font-size: rem(14);
  line-height: 1;
  letter-spacing: 1px;
  padding: rem(6) rem(5);
  color: #000;
}
.card__date{
  font-size: rem(14);
  line-height: 1;
  letter-spacing: 1px;
  margin-right: rem(2);
  @include mq(md) {
    margin-right: initial;
  }
}
.card:hover .card__date{
  color: $white;
}